

















































































.language {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  color: #868686;
  font-weight: bold;
  transition: .2s;
  &:hover {
    color: #333;
  }
}

.language__icon {
  font-size: 1.2rem;
}

.language__inner {
  margin-left: 7px;
  .language__name {
    font-size: .8rem;
    color: #555;
  }
  .language__current {
    font-size: .7rem;
    color: #868686;
  }
}

.lang-item {
  display: inline-block;
  padding: 2px 10px;
  font-size: 1rem;
  margin: 5px;
  color: #666;
  font-weight: bold;
  &:hover {
    color: #333;
  }
}

.white {
  .language {
    color: rgba(255, 255, 255, .8);
    &:hover {
      color: #fff;
    }
  }
  .language__name {
    color: rgba(255, 255, 255, .8);
  }
  .language__current {
    color: rgba(255, 255, 255, .7);
  }
}
